import React, { useState, useRef, useEffect } from 'react';
import {Blog} from './Blog'

import axios from "axios";

export const BlogHandler=(props)=>{
    const [myData, setMyData] = useState([])
    const [loading, setLoading] = useState(false)
    var option = {
      method: 'GET',
      url: 'https://karlson.herokuapp.com/'
    };
    useEffect(()=>{
      setLoading(true)
      axios
      .request(option)
      .then(function(response) {
        let data = JSON.stringify(response.data.data)
        setMyData(JSON.parse(data))
        console.log("Succesfully fetched all posts!")
      })
      .catch(function(err) {
        console.log(err);
      })
      .finally(()=>{
        setLoading(false)
      })
    }, [])

    if (loading) return <div className="loader"><span className="load"></span><span className="load"></span><span className="load"></span></div>;
    
    return (
        <div className="container text-start">
        <br/>
        <h1>Blogposts By Eklavya:</h1><hr/>
       	{myData.map((post)=>{
             return <Blog key = {post.sno} mypost = {post}/>
         })}
         </div>
 )}   